import React from 'react';
import {Typography,Card,CardContent} from '@material-ui/core';
import CountUp from 'react-countup';
import styles from './../Cards/cards.module.css';
import logo from './../Images/india.jpg';
//import Typography from '@material-ui/core/Typography';
const DataCards =(props)=>{ 
  const data=props.data;                
      const dataCard =  
      <React.Fragment>
        <Typography className={styles.titleDisplayer} variant="h3"> 
        <img alt="India" className={styles.imageDisplayer} src={logo} /> COVID-19 DASHBOARD        
        </Typography><br/>
        <Typography  align="center">
          {new Date(props.lastUpdate).toDateString()} {new Date(props.lastUpdate).toLocaleTimeString()}
        </Typography> 
        <div className={styles.cardDisplay}>
        <Card className={styles.noShadow}>
        {Object.keys(data).map((d,index)=>( 
      <CardContent key={index} className={styles.individualContent}>                     
        <Typography variant="h5" align="center">
        <CountUp className={d === 'Active' ? styles.active : d === 'Confirmed' ? styles.confirmed : 
        d === 'Deaths' ? styles.deaths : styles.recovered} start={0} 
        end={data[`${d}`]} duration = {3} separator=","/>
          </Typography>
          <Typography variant="overline" align="center" className={styles.caseText}>
          {d} 
        </Typography>          
      </CardContent>))}     
    </Card>
    </div>
          {/* <Grid container spacing={4} justify="center">                
        <Grid item component={Card} className={styles.cardDisplay}>
          {Object.keys(data).map((d,index)=>(  
          <CardContent key={index}>
          <Typography variant="body1" align="center" className={styles.caseText}>
          {d} 
        </Typography> 
        <Typography variant="h5" align="center">
        <CountUp className={styles.caseNumber} start={0} 
        end={data[`${d}`]} duration = {3} separator=","/>
          </Typography>               
          </CardContent> 
          ))}     
        </Grid>
    </Grid><br/>
    <Typography variant="h6" align="center">
          {new Date(props.lastUpdate).toDateString()} {new Date(props.lastUpdate).toLocaleTimeString()}
        </Typography> */}     
      </React.Fragment>
    return(      
        <div>
               {dataCard}   
        </div>
    )
}
export default DataCards;